@import "../../../scss/settings";

.registerPage {
  @extend %main_background;

}


.registerBox {
  @extend %container;
  width: 100%;
  margin: 60px;
  padding: 60px;

  @extend %errorMessageColor;

  @media (max-width: 800px) {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    display: grid;
    justify-items: center;

    h1 {
      font-size: 40px;
      text-align: center;
    }

  }
}


